
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'PluginList',
  props: {
    msg: String,
  },
  methods: {
    browseQRCode() {
      var that = this.$core;
      this.$core.browseQRCode({
        callback: function (result: any) {
          that.alertDialog(result);
        }
      });
    },
    scanQRCode() {
      var that = this.$core;
      this.$core.scanQRCode({
        callback: function (result: any) {
          that.alertDialog(result);
        }
      });
    },
    appInfo() {
      var that = this.$core;
      this.$core.appInfo({
        callback: function (result: any) {
          that.alertDialog(result);
        }
      });
    },
    showDatePicker() {
      var that = this.$core;
      that.showDatePicker({
        callback: function (result: any) {
          that.alertDialog(result);
        }
      });
    },
    exitApp() {
      this.$core.exitApp();
    },
    externalBrowser() {
      this.$core.externalBrowser({
        url: 'https://www.youtube.com/'
      });
    },
    biometricAuthentication() {
      var that = this.$core;
      this.$core.biometricAuthentication({
        title: 'Verify it\'s you',
        description: 'Use your biometric to continue',
        cancel_text: 'Cancel',
        confirm_require: true,
        allow_device_credential: true,
        callback: function (result: any) {
          that.alertDialog(result);
        }
      });
    },
    showToast() {
      var that = this.$core;
      this.$core.showToast({
        message: 'Hello JS',
        duration: 'SHORT',
      });
    },
    camera() {
      var that = this.$core;
      this.$core.showCamera({
        callback: function (result: any) {
          that.alertDialog(result);
        }
      });
    },
    speechToText() {
      var that = this.$core;
      this.$core.speechToText({
        language: 'km_KH',
        description: 'កំពុងស្ដាប់...',
        callback: function (result: any) {
          that.alertDialog(result);
        }
      });
    }
  },
});
